


















































import uniIcon from "@/components/uni-icon.vue"
import uniDrawer from "@/components/uni-drawer.vue"
export default {
	data() {
		return {
			winWidth: '',
			winHeight: '',
			secHeight: '',
			drawerBlean: false,
			menuBlean: false,
			PageStartX: '',
			PageEndX: '',
			PageStratY: '',
			PageEndY: '',
			dayContent: {
				title: "",
				author: "",
				content: "",
				date: "",
				digest: ""
			},
			HTitle: false,
		}
	},
	components: {
		uniIcon,
		uniDrawer
	},
	onLoad(e) {
		if (uni.getSystemInfoSync().platform === 'devtools') {
			console.log('运行在开发工具上')
			var that = this
			wx.getSystemInfo({
				success: function(res) {
					console.log(res.windowWidth);
					console.log(res.windowHeight);
					that.winHeight = res.windowHeight
					that.winWidth = res.windowWidth;
					that.secHeight = res.windowHeight - 40
				},
			})
		} else {
			var that = this
			wx.getSystemInfo({
				success: function(res) {
					console.log(res.windowWidth);
					console.log(res.windowHeight);
					that.winHeight = res.windowHeight
					that.winWidth = res.windowWidth;
					that.secHeight = res.windowHeight - 40
				},
			})
		}
		// 请求数据
		var that = this.dayContent;
		uni.request({
			url: "https://interface.meiriyiwen.com/article/today?dev=1",
			dataType: "JSON",
			success: function(e) {
				var res = e.data.data;
				// console.log(res);
				// res.content = res.content.replace(/\"/g, "");
				that.author = res.author;
				that.content = res.content;
				that.digest = res.digest;
				that.title = res.title;
				that.date = res.date;
				// console.log(that.author, that.content, that.digest, that.title, that.date)
			}
		})
	},

	methods: {
		qingqiu:function(){
				uni.request({
					url: "https://interface.meiriyiwen.com/article/today?dev=1",
					dataType: "JSON",
					success: function(e) {
						var res = e.data.data;
						// console.log(res);
						res.content = res.content.replace(/\"/g, "");
						that.author = res.author;
						that.content = res.content;
						that.digest = res.digest;
						that.title = res.title;
						that.date = res.date;
						// console.log(that.author, that.content, that.digest, that.title, that.date)
					}
				})
		},
		// 控制抽屉栏
		drwer: function() {
			this.menuBlean = false;
			if (!this.drawerBlean) {
				this.drawerBlean = true;
			} else if (this.drawerBlean) {
				this.drawerBlean = false;
			}
			console.log('抽屉开关=' + this.drawerBlean)
		},
		// 控制菜单
		menu: function() {
			this.drawerBlean = false;
			if (!this.menuBlean) {
				this.menuBlean = true;
			} else {
				this.menuBlean = false;
			}
			console.log('菜单=' + this.menuBlean)
		},
		// 判断左右滑动
		touchStrat: function(e) {
			// console.log(e);
			// 小程序规范

			if (uni.getSystemInfoSync().platform === 'devtools') {
				this.PageStartX = e.clientX
				this.PageStartY = e.clientY
			} else {
				this.PageStartX = e.clientX
				this.PageStartY = e.clientY
			}

			// H5平台规范




			// app规范




		},
		touchMove: function(e) {

			if (uni.getSystemInfoSync().platform === 'devtools') {
				this.PageEndX = e.clientX
				this.PageEndY = e.clientY
			} else {
				this.PageEndX = e.clientX
				this.PageEndY = e.clientY
			}







			// app规范




		},
		touchEnd: function(e) {
			// console.log(this.winWidth)
			// 当菜单没有被点开
			if (!this.menuBlean) {
				// 手指到最左边才执行	
				if (this.PageStartX < 100) {

					let diffVal = this.PageStartX - this.PageEndX;
					console.log('开始坐标：' + this.PageStartX, '结束坐标：' + this.PageEndX)
					console.log("cha:" + diffVal)

					if (diffVal < -100 && diffVal != this.PageStartX) {
						this.menuBlean = true;

					}
					// 手指到最右边才执行
				} else if (this.winWidth - this.PageStartX < 100) {

					let diffVal = this.PageStartX - this.PageEndX;

					if (diffVal > 100 && diffVal != this.PageStartX) {
						this.drawerBlean = true;
					} else if (diffVal < 100) {
						this.drawerBlean = false;
					}
				} else {
					return false;
				}
				//当菜单被点开 
			} else if (this.menuBlean) {
				console.log(1);

			}
		},

		// 滚动
		srcTop: function(e) {
			console.log(e);
			console.log("123131313")
		}
	},
	// 收藏
	coll: function() {
		// this.callIcon?"star":"star-filled";
		console.log(1)
	}
}



